import React, { useState, forwardRef} from 'react'
import {Button, Input} from '@material-ui/core'
import "./TodoItem.css"

import { useDispatch } from 'react-redux'
import { deleteTodo, updateTodo } from './redux/actions'

export default forwardRef (function Todoitem({todo}, ref) {
   
    const [time, setTime] = useState(new Date().toLocaleString())
    const [checked, setChecked] = useState(false)
    const [edit, setEdit] = useState(false)
    const [ name, setName] = useState(todo.name)
   
    const dispatch = useDispatch()
    return (
        <div ref={ref} className="todoitem">
     
            <div className="todoitem__title">
            
        {edit ? <Input type="text" value={name} onChange={(e) => setName(e.target.value)}/>: <li className="todo__list">{todo.name}</li>}   
                
          {name ?  <p className="todoitem__update">Posted at {time}</p> : <p className="todoitem__update">posted at {time}</p>}     
          
            </div>
                 <input type="checkbox" onClick= {() => setChecked(!checked)} className="input"/>
           
         {
             checked ? 
             <div>
             <Button className="edit" onClick={() => { dispatch(updateTodo({
                 ...todo,
                 name : name,
                createdOn: new Date().toString()
                }))
                if(edit) {
                    setName(todo.name)
                    
                }
                setEdit(!edit)}}>
                    {edit ? "Update" : "Edit"}
                   
                    </Button>
                

            <Button className="delete" onClick={() => dispatch(deleteTodo(todo.id))}>Delete</Button>
            </div> : 
                        <p className="todo__name">{todo.name}</p>
            }
           
              
        </div>
    )
})


